@font-face {
  font-family: Inter;
  src: url("https://fonts.googleapis.com/css2?family=Inter:wght@200;500&display=swap");
}

:root {
  --main-bg-color: #fff;
  --second-color: #fff;
  --second-text-color: #999;
}

.chats_navigation {
  width: 310px;
  height: 100%;
  border: 1px #eaeaea;
  border-right-style: solid;
  flex-flow: column;
  padding: 0 10px;
  display: flex;
}

.navigation_profile {
  margin-bottom: 10px;
}

.profile_button {
  text-align: right;
  width: 100%;
  cursor: pointer;
  border: none;
  border-radius: 8px;
  margin: auto;
  font-family: Inter, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.profile_button__default {
  background: var(--second-color);
  color: var(--second-text-color);
  margin-top: 21px;
  font-size: 12px;
}

.text:active, .text:hover, .text {
  color: #666;
  text-decoration: none;
}

.navigation_search {
  text-align: center;
  background-color: #efefef;
  border-radius: 5px;
  margin-bottom: 10px;
  font-family: Inter, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.search_text {
  color: var(--second-text-color);
  height: 32px;
  font-size: 12px;
  line-height: 32px;
}

.navigation_list {
  margin: auto;
}

.chats_content {
  flex: 1;
  display: flex;
}

.content_text {
  font-family: Inter, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.content_text__default {
  color: var(--second-text-color);
  height: 12px;
  margin: auto;
  font-size: 12px;
  line-height: 12px;
}

